.experience {
  text-align: center;
  width: 100%;
  padding-bottom: 50px;
}

.experienceHeader {
  font-size: 50px;
  font-family: "Merriweather";
  font-weight: 700;
  color: #707070;
  padding-bottom: 40px;
}

.tiles {
  text-align: center;
}

.description {
  width: 100%;
  color: #969696;
}
.title {
  color: #969696;
  font-size: 45px;
  font-family: Courier New;
  margin-top: -150px;
  margin-bottom: 1%;
}

.location {
  color: #4b9cd3;
  font-size: 30px;
  font-family: Courier New;
}

.dates {
  color: #4b9cd3;
  font-size: 16px;
  font-family: Courier New;
  text-align: right;
  padding-right: 5%;
}

.text {
  margin-left: 4%;
  width: 90%;
  color: #969696;
  font-family: "Lato";
  font-size: 25px;
  line-height: 40px;
  list-style-position: outside;
  text-align: left;
}

.list {
  padding: 10px 20px;
}

@media screen and (min-width: 995px) {
  .experience {
    width: 100%;
    padding-top: 10px;
  }

  .experienceHeader {
    font-size: 40px;
    font-family: "Merriweather";
    font-weight: 700;
    color: #707070;
    padding-bottom: 3%;
  }

  .tiles {
    text-align: center;
  }

  .description {
    width: 100%;
    color: #969696;
  }
  .title {
    color: #969696;
    font-size: 28px;
    font-family: Courier New;
    margin-top: -160px;
  }

  .location {
    color: #4b9cd3;
    font-size: 25px;
    font-family: Courier New;
  }

  .dates {
    color: #4b9cd3;
    font-size: 20px;
    font-family: Courier New;
    text-align: right;
  }

  .text {
    color: #969696;
    font-family: "Lato";
    font-size: 24px;
    line-height: 25px;
  }

  .list {
    padding: 5px 20px;
  }
}
