.courses {
  text-align: center;
}
.coursesHeader {
  font-size: 50px;
  font-family: "Merriweather";
  font-weight: 700;
  color: #707070;
  padding-bottom: 100px;
}

.tiles {
  margin-top: -70px;

}

.title{ 
  font-size: 30px;
  padding-top: 50px;
}
.classes {
  margin-top: 150px;
}

.classNames {
  color: #969696;
  font-family: "Lato";
  font-size: 25px;
  line-height: 1;
  text-align: left;
  padding-left: 20%;
}

@media screen and (min-width: 995px) {
  .coursesContainer {
    text-align: center;
  }
  .coursesHeader {
    font-size: 40px;
    font-family: "Merriweather";
    font-weight: 700;
    color: #707070;
  }

  .tiles {
  }
  .classes {
    margin-top: 130px;
  }

  .classNames {
    color: #969696;
    font-family: "Lato";
    font-size: 24px;
    line-height: 1;
    text-align: center;
    padding-right: 20%;
    list-style-position: inside;
  }
  .list {
    padding: 5px 20px;
  }
}
