.about {
  margin-top: 1.5%;
  text-align: center;
}

.info {
  text-align: center;
}

.builtBy {
  width: 100%;
  /*writing-mode: vertical-lr;*/
  padding-top: 3%;
  padding-left: 1%;
  font-family: "Courier New";
  color: #2f548f;
  font-weight: 700;
  font-size: 20px;
}

@media (min-width: 995px) {
  .body {
    padding-left: 5%;
  }

  .about {
    width: 40%;
    position: fixed;
    float: left;
  }

  .info {
    width: 70%;
    float: left;
    padding-left: 25%;
  }

  .builtBy {
    writing-mode: tb-rl;
    position: fixed;
    bottom: 0;
    right: 0;
    font-family: "Courier New";
    color: #2f548f;
    font-weight: 700;
    font-size: 20px;
    width: 5%;
  }
}
