.aboutContainer {
  text-align: center;
  height: 100%;
}

.hi {
  font-size: 40px;
  font-family: Courier New;
  font-weight: 300;
  color: #707070;
}

.name {
  margin-top: -10px;
  font-size: 60px;
  font-family: "Arial"; 
  font-weight: 600;
  color: #17669c;
  line-height: 0;
}

.profile {
  margin-top: -20px;
  width: 30%;
  height: auto;
}

.paragraph {
  width: 94%;
  font-family: "Lato";
  font-weight: 200;
  font-size: 22px;
  padding-left: 3%;
  color: #969696;
}


@media screen and (min-width: 995px) {
  .aboutContainer {
  }
  
  .hi {
    font-size: 40px;
    font-family: "Courier New";
    font-weight: 300;
    color: #707070;
    position: absolute;
    width: 100%;
  }
  
  .name {
    font-size: 50px;
    font-family: "Arial";
    font-weight: 600;
    color: #17669c;
    line-height: 1.2;
    position: relative; 
    padding-top: 3%;
    margin-bottom: 4%;
    top: 10vh;
    width: 100%;
  }
  .image{
    width: 100%;
    position: relative;
    top: 10vh;
    margin-bottom: -3%;
  }
  
  .profile{
    width: 200px;
  }
  
  .paragraph {
    font-family: "Lato";
    font-weight: 200;
    font-size: 20px;
    color: #969696;
    width: 94.5%;
    position: relative; 
    top: 10vh;
  }

  .aboutIcon{
    position: relative; 
    top: 10vh;
  }


}


